<script type="text/ecmascript-6">
    import config from '@/assets/config';
    import domtoimg from 'dom-to-image';
    import axios from 'axios';
    import html2canvas from 'html2canvas';
    import Tool from '@/assets/tool'

    export default {
        name: 'Result',
        data () {
            return {
                name: '',
                phy_age: 0,
                psy_age: 0,
                selected_list: [],
                psy_selected: '',
                phy_selected: '',
                psy_period: '',
                phy_period: '',
                config,
                imgSaveTouchSrc: '',
                desc:'',
                pass:-1,
                is_wx:false,
                is_app:false,
                notice:'长按保存图片'
            };
        },
        created () {
            let record = localStorage.getItem('siguoya:87HMhW2T9u');
            // record='[0,2,1,0,2,1]'
            if (!record) {
                this.$router.push({
                    path: '/'
                });
                return;
            }
            this.selected_list = JSON.parse(record);
            this.psy_selected = this.selected_list[0].toString() + this.selected_list[1] + this.selected_list[2];
            this.phy_selected = this.selected_list[3].toString() + this.selected_list[4] + this.selected_list[5];
            this.psy_period = this.getPsyPeriod(this.psy_selected);
            this.phy_period = this.getPhyPeriod(this.phy_selected);
            this.psy_age = this.getAge(this.psy_period);
            this.phy_age = this.getAge(this.phy_period);
            this.name=this.getUserName()
            let period=config['intro'][this.psy_period+this.phy_period]
            this.desc=period['desc'][Math.floor(Math.random() *4)]
            this.pass=period['pass']
            if(!(Tool.is_wx() || Tool.is_cosleep() || Tool.is_QQ())){
                this.notice='截图保存图片'
            }
            this.is_wx=Tool.is_wx()
            this.is_app=Tool.is_cosleep()
            // window.onresize=()=>{
            //     if(Tool.is_wx()){
            //         this.$nextTick(()=>{
            //             this.$refs.resultFrame.style['transform-origin']='top'
            //             this.$refs.resultFrame.style.transform=`scale(0.85)`
            //         })
            //     }
            // }
        },
        mounted () {
            setTimeout(() => {
                let phy_age=this.phy_age>=100?100:this.phy_age
                this.$refs.phy.style.width = phy_age + '%';
                let psy_age=this.psy_age>=100?100:this.psy_age
                this.$refs.psy.style.width = psy_age + '%';
                this.createTouchImg2();
                if(window.innerHeight<510){
                    this.$refs.resultBgImg.style['max-height']='330px'
                }
            }, 300);
        },
        methods: {
            toChild(){
              this.$router.push({
                  'path':'/children'
              })
            },
            getUserName(){
                let nickname
                if (Tool.is_wx()){
                    // return 'aaa'
                    nickname=JSON.parse(localStorage.getItem('siguoya:Gy6jKG2W26'))['data']['nickname']
                    return nickname
                }else if(Tool.is_cosleep()){
                    let record=JSON.parse(decodeURIComponent(localStorage.getItem('siguoya:79XYF7kQ4z')))
                    if(typeof record=='string'){
                        record =JSON.parse(record)
                    }
                    nickname=record['data']['name']
                    return nickname
                }else {
                    return localStorage.getItem('siguoya:G386FDNyK7')
                }
            },
            base64ToImg (data) {
                let record = JSON.parse(localStorage.getItem('siguoya:6f87H4FAig'));
                axios({
                    url: `http://upload.qiniu.com/putb64/-1`,
                    method: 'post',
                    headers: {
                        'Content-Type': 'application/octet-stream',
                        'Authorization': `UpToken ${record['upToken']}`
                    },
                    data: data.split(',')[1]
                }).then((resp) => {
                    this.imgSaveTouchSrc = `http://game.psy-1.com/${resp['data']['key']}`;
                }).catch((error) => {
                    console.log(error);
                });
            },
            createTouchImg2 () {
                html2canvas(document.getElementById('screenshot'),{
                    backgroundColor:null,
                    scale:3
                }).then((canvas) => {
                    this.imgSaveTouchSrc = canvas.toDataURL('image/png');
                });
            },
            createTouchImg () {
                if (/iphone|ipad|itouch/i.test(navigator.userAgent)) {
                    if (typeof window.webkit.messageHandlers.XinchaoApp != 'undefined') {
                        domtoimg.toPng(document.getElementById('screenshot')).then((data) => {
                            this.base64ToImg(data);
                        }).catch((error) => {
                            // console.log('ios之小睡眠APP图片转换失败', error);
                        });
                    } else {
                        domtoimg.toSvg(document.getElementById('screenshot')).then((data) => {
                            this.imgSaveTouchSrc = data;
                        }).catch((error) => {
                            // console.log('ios图片转换失败', error);
                        });
                    }
                } else {
                    domtoimg.toPng(document.getElementById('screenshot')).then((data) => {
                        this.base64ToImg(data);
                        // this.imgSaveTouchSrc=data
                    }).catch((error) => {
                        // console.log('图片转换失败', error);
                    });
                }
            },
            getAge (period) {
                let random = Math.random(), age;
                switch (period) {
                    case 'a':
                        age = random * 13;
                        break;
                    case 'b':
                        age = 14 + random * 16;
                        break;
                    case 'c':
                        age = 31 + random * 29;
                        break;
                    case 'd':
                        age = 61 + random * 60;
                        break;
                }
                age=Math.round(age)
                if(age<=8){
                    return '满月';
                }
                return age
            },
            getPsyPeriod (psy_selected) {
                for (let period in config['psy']) {
                    for (let i = 0; i < config['psy'][period].length; i++) {
                        if (config['psy'][period][i] == psy_selected) {
                            return period;
                        }
                    }
                }
            },
            getPhyPeriod (phy_selected) {
                for (let period in config['phy']) {
                    for (let i = 0; i < config['phy'][period].length; i++) {
                        if (config['phy'][period][i] == phy_selected) {
                            return period;
                        }
                    }
                }
            }
        },
        watch: {}
    };
</script>
