﻿<template>
    <div class="children">
        <div class="header">
            <img src="./g4.png" alt="">
        </div>
        <div class="intro">
            <div class="group">
                <p>听到什么样的声音</p>
                <p>你最能回忆起童年的美好时光？</p>
            </div>
            <div class="group">
                <p>是夜虫唧唧、蛙鸣蝉噪</p>
                <p>还是风铃蹁跹、老屋的钟？</p>
            </div>
            <div class="group">
                <p>超过300种原创音效</p>
                <p>带你穿越时空、声临其境！</p>
            </div>
        </div>
        <div class="footer" @click="download">
            <div class="wrapper">
              <p class="font" style="position: absolute">回到童年</p>
                <img src="./button@3x.png" alt="">
            </div>
        </div>
    </div>
</template>
<script type="text/ecmascript-6">
    import Tool from '@/assets/tool';
	export default {
		name:"Children",
        methods:{
            download(){
                let _hmt = _hmt || [];
                if(Tool.is_wx()){
                    _hmt.push(['_trackEvent', '小睡眠儿童节活动', '点击下载', '回到童年-微信渠道']);
                }else if(Tool.is_cosleep_ios()){
                    _hmt.push(['_trackEvent', '小睡眠儿童节活动', '点击下载', '回到童年-小睡眠IOS版渠道']);
                }else if(Tool.is_cosleep_android()){
                    _hmt.push(['_trackEvent', '小睡眠儿童节活动', '点击下载', '回到童年-小睡眠Android版渠道']);
                }else {
                    _hmt.push(['_trackEvent', '小睡眠儿童节活动', '点击下载', '回到童年-其他渠道']);
                }
                window.location.href='https://www.heartide.com/download/smallsleep'
            }
        }
    }
</script>
<style lang="stylus" rel="stylesheet/stylus">
    @import '~@/assets/mixin.styl'
    px2html(value)
      return unit(value / 37.5, 'rem')
    .app-bg
        background none !important
        background-color #fefcef !important
    .children
        display flex
        flex-direction column
        justify-content  center
        height 100%
        position relative
        .header
            flex 1 0 auto
            text-align center
            img
                width 100%
                height auto
                max-width 18.77rem
        .intro
            flex 1 0 auto
            .group
                margin-bottom .4rem
            p
                margin-bottom  .25rem
                font-size .42rem
                color rgba(16, 7, 57, 1)
                font-weight normal
                text-align center
        .footer
            flex 1 0 auto
            position relative
            font-weight bold
            text-align center
            .wrapper
                display inline-block
                position relative
                .font {
                  position absolute
                  top 45%
                  left 50%
                  width 100%
                  transform translateY(-50%) translateX(-50%)
                  color rgba(255, 255, 255, 1)
                  font-size px2html(15px)
                }
                .notice
                    font-dpr(.53rem)
                    color #754c24
                    position absolute
                    left 50%
                    top 50%
                    transform translate(-50%,-50%)
                img
                    width 5.6rem
                    height auto
</style>



