﻿
<template>
    <div class="ques">
        <div class="page">
            <img src="./number_bg@3x.png" alt="">
            <span class="page-progress">{{current_page+1}}/6</span>
            <span class="change">{{title}}</span>
        </div>
        <div class="ques-body">
            <div class='wjh-top'></div>
            <!-- <div class="ques-title">
                <p>{{title}}</p>
            </div> -->
            <div class="ques-cover">
                <img :src="cover" alt="">
            </div>
            <div v-for="option,index in options" @click="selectOption(index)" class="ques-option">
                <div class="img-wrapper">
                    <div class="backdrop"></div>
                    <img class="option-frame" src="./free.png" alt=""> <!-- :src="config['option_imgs'][index]" -->
                    <img v-show="index==current_selected" class="option-selected" src="./open.png" alt=""> <!-- src="./childrenday_question_select.png" -->
                    <span>{{ toABCD(index) }}{{option}}</span>
                </div>
            </div>
        </div>
    </div>
</template>
<script type="text/ecmascript-6">
    import config from '@/assets/config';

    export default {
        name: 'Ques',
        data () {
            return {
                title: '',
                current_page: 0,
                cover: '',
                current_selected: -1,
                options: [],
                config,
                enable: true,
                selected_list:[]
            };
        },
        created () {
            this.renderConfig();
        },
        methods: {
            toABCD (index) {
              let number = ('' + index).charCodeAt()
              number += 17
              return String.fromCharCode(number) + '.'
            },
            selectOption (index) {
                this.current_selected = index;
                if (this.enable) {
                    this.enable = false;
                    this.selected_list.push(this.current_selected)
                    setTimeout(() => {
                        if (this.current_page < 5) {
                            this.current_page += 1;
                            this.current_selected = -1;
                            this.enable = true;
                        }else {
                            localStorage.setItem("siguoya:87HMhW2T9u",JSON.stringify(this.selected_list))
                            this.$router.push({
                                'path':'/result'
                            })
                            // console.log('得出的最终结果为',...this.selected_list)
                        }
                    }, 500);
                }
            },
            renderConfig () {
                this.title = config['page'][this.current_page]['title'];
                this.cover = config['page'][this.current_page]['cover'];
                this.options = config['page'][this.current_page]['options'];
            },
            selected () {

            }
        },
        watch: {
            current_page () {
                this.renderConfig();
            }
        }
    };
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
    @import '~@/assets/mixin.styl'
    $fixed-size = 20px
    px2html(value)
      return unit(value / 37.5, 'rem')

    .ques
        // position fixed
        // top $fixed-size
        // bottom $fixed-size
        // left $fixed-size
        // right $fixed-size
        position relative
        .page
            margin-top px2html(33px)
            position relative
            text-align left
            img
                width px2html(46px)
                height px2html(25px)
                height auto
            .page-progress
                position absolute
                left 5%
                top 45%
                transform translate(-50%, -50%)
                font-size px2html(14px)
                color rgba(152, 222, 216, 1)
            .change
              top 45%
              transform translateY(-50%)
              position absolute
              color rgba(16, 7, 57, 1)
              font-size px2html(19px)
        .ques-body
            // position fixed
            // top 20px
            // left 20px
            // right 20px
            // bottom 20px
            display flex
            flex-direction column
            .wjh-top {
              width 100%
              height px2html(25px)
            }
        .ques-title
            flex 1 1 50px
            text-align center
            font-dpr(0.5rem)
            color #754c24
            font-weight bold
            position relative
            p
                position absolute
                left 0
                width 100%
                text-align center
                bottom 10%
                margin-bottom 0
        .ques-cover
            flex 1 0 auto
            text-align center
            img
                height px2html(207px)
        .ques-option
            flex 1 0 auto
            text-align center
            position relative
            display inline-block
            .img-wrapper
                display inline-block
                position relative
                .backdrop
                    position absolute
                    left 0
                    top 0
                    width 100%
                    height 100%
                    background-color red
                    opacity 0
            .option-frame
                width px2html(322px)
                height px2html(55px)
            .option-selected
                position absolute
                left 0
                top 0
                width 100%
                height auto
            span
                text-align left
                width px2html(400px)
                position absolute
                left 3%
                top 50%
                margin 0
                font-size px2html(18px)
                color rgba(16, 7, 59, 1)
                transform translate(0, -50%)
</style>



