﻿
<template>
    <div id="app">
        <div class="wjh-bg bg-top"></div>
        <div class="wjh-bg bg-bottom"></div>
        <div class="app-bg"></div>
        <div class="app-frame">
            <router-view/>
        </div>
        <input type="hidden" name="ShareAppMessage" :value="ShareAppMessage">
        <input type="hidden" name="ShareTimeline" :value="ShareTimeline">
        <input type="hidden" name="xinchao_share" :value="xinchao_share">
    </div>
</template>
<script type="text/ecmascript-6">
    import axios from 'axios';
    import Tool from '@/assets/tool';
    import config from '@/assets/config';

    const share_url = encodeURIComponent(location.href.split('#')[0]);
    export default {
        name: 'App',
        data () {
            return {
                ShareAppMessage: '',
                ShareTimeline: '',
                xinchao_share: ''
            };
        },
        created () {
            this.share_config = {
                title: '你有资格过儿童节吗？',
                desc: '千万人参与，朋友圈爆款刷屏，你有资格过儿童节吗？',
                link: 'https://www.heartide.com/cosleep/childrenday/',
                imgUrl: 'https://res.psy-1.com/childrenday_start_img.png' // 分享图标
            };
            window.Xinchao={}
            Xinchao.Web={}
            this.disableWxFontScale();
            this.registerWechatEvent();
            this.registerApp();
            this.preload();
            if(Tool.is_wx){
                this.getWeixinConfig();
            }
            this.setHeartideAppShare();
            this.setQQShare()
            this.statics()
        },
        mounted () {

        },
        methods: {
            statics(){
                var hm = document.createElement('script');
                hm.src = 'https://hm.baidu.com/hm.js?c6a0be7a2e98e04ca2cf523568e2cbc4';
                var s = document.getElementsByTagName('script')[0];
                s.parentNode.insertBefore(hm, s);
            },
            setQQShare(){
                setShareInfo({
                    title:          this.share_config['title'],
                    summary:        this.share_config['desc'],
                    pic:            this.share_config['imgUrl'],
                    url:            this.share_config['link'],
                });
            },
            registerWechatEvent () {
            },
            getWeixinConfig () {
                axios.get(`${Tool.get_api_root()}/web/v1/wechat/config?url=${share_url}`)
                    .then((response) => {
                        wx.config(response['data']['data']['wechat_config']);
                        wx.ready(()=>{
                            wx.onMenuShareAppMessage(this.share_config);
                            wx.onMenuShareTimeline(this.share_config);
                        });
                        wx.error((res) => {
                            console.log('wx config error', res);
                        });
                    });
            },
            setHeartideAppShare () {
                let ShareAppMessage = {
                    'title': this.share_config['title'],
                    'desc': this.share_config['desc'],
                    'link': this.share_config['link'],
                    'imgUrl': this.share_config['imgUrl'],
                    'shareType': 1
                };
                let ShareTimeline = {
                    'title': this.share_config['title'],
                    'link': this.share_config['link'],
                    'imgUrl': this.share_config['imgUrl'],
                    'shareType': 2
                };
                let app_share = {
                    'type1': ShareAppMessage,
                    'type2': ShareTimeline,
                    'type3': {
                        'imgUrl': '',
                        'shareType': 3
                    }
                };
                this.xinchao_share = JSON.stringify(app_share);
                this.ShareTimeline = JSON.stringify(ShareTimeline);
                this.ShareAppMessage = JSON.stringify(ShareAppMessage);
            },
            preload () {
                let images = [];
                for (let i = 0; i < config['option_imgs'].length; i++) {
                    images.push(config['option_imgs'][i]);
                }
                for (let i = 0; i < config['page'].length; i++) {
                    images.push(config['page'][i]['cover']);
                }
                images.push('https://webres.psy-1.com/sgy/dist/static/img/childrenday_cosleep_bg.691876e.png');
                for (let i = 0; i < images.length; i++) {
                    let img = new Image();
                    img.src = images[i];
                }
            },
            registerApp () {
                window.xinchaoAppOnload = () => {

                };
            },
            disableWxFontScale () {
                if (typeof WeixinJSBridge == 'object' && typeof WeixinJSBridge.invoke == 'function') {
                    handleFontSize();
                } else {
                    if (document.addEventListener) {
                        document.addEventListener('WeixinJSBridgeReady', handleFontSize, false);
                    } else if (document.attachEvent) {
                        document.attachEvent('WeixinJSBridgeReady', handleFontSize);
                        document.attachEvent('onWeixinJSBridgeReady', handleFontSize);
                    }
                }

                function handleFontSize () {
                    // 设置网页字体为默认大小
                    WeixinJSBridge.invoke('setFontSizeCallback', {'fontSize': 0});
                    // 重写设置网页字体大小的事件
                    WeixinJSBridge.on('menu:setfont', function () {
                        WeixinJSBridge.invoke('setFontSizeCallback', {'fontSize': 0});
                    });
                }
            }
        }
    };
</script>
<style lang="stylus" rel="stylesheet/stylus">
    $app-body-position = 20px
    $app-body-circle = 20px
    ﻿body /* IOS禁止微信调整字体大小 */
        -webkit-text-size-adjust: 100% !important;
        text-size-adjust: 100% !important;
        -moz-text-size-adjust: 100% !important;

    px2html(value)
      return unit(value / 37.5, 'rem')


    .wjh-bg {
      width px2html(110px)
      height px2html(162px)
      position fixed
    }
    .bg-top {
      background url("./assets/bg_top@3x.png")
      background-size 100% 100%
      top 0px
      right 0px
    }
    .bg-bottom {
      background url("./assets/bg_down@3x.png")
      background-size 100% 100%
      bottom 0px
      left 0px
    }

    #app
        position fixed
        top 0
        left 0
        width 100%
        height 100%
        // min-height px2html(557px)
        // overflow-y scroll
        background: rgba(152, 222, 216, 1)
        font-family Helvetica, PingFang SC, source hans sans
        -webkit-font-smoothing antialiased
        -moz-osx-font-smoothing grayscale
        .app-bg
            // position fixed
            // top $app-body-position
            // bottom $app-body-position
            // left $app-body-position
            // right $app-body-position
            // background: #FEFCEF
            // background: radial-gradient(circle at top left, transparent $app-body-circle, #FEFCEF 0) top left,
            //    radial-gradient(circle at top right, transparent $app-body-circle, #FEFCEF 0) top right,
            //    radial-gradient(circle at bottom right, transparent $app-body-circle, #FEFCEF 0) bottom right,
            //    radial-gradient(circle at bottom left, transparent $app-body-circle, #FEFCEF 0) bottom left
            background-size: 51% 51%
            background-repeat: no-repeat
            box-shadow 3px solid #FEFCEF
            opacity .6
        .app-frame
            width 100%
            height 100%
            // position fixed
            // top 20px
            // bottom 20px
            // left 20px
            // right 20px
</style>



