export default class Tool{
    static is_wx(){
        return /micromessenger/i.test(navigator.userAgent)
    }
    static is_QQ(){
        return /QQ/.test(navigator.userAgent)
    }
    static is_cosleep(){
        return this.is_cosleep_android() || this.is_cosleep_ios()
    }
    static get_api_root(){
        return process.env.NODE_ENV === 'production' ? 'https://api.psy-1.com' : 'https://api.debug.psy-1.com';
    }
    static get_web_root(){
        return process.env.NODE_ENV === 'production' ? 'https://www.heartide.com' : 'https://web.debug.psy-1.com';
    }
    static get_wechat_app_id(){
        return process.env.NODE_ENV === 'production' ? 'wx2dbf7017998b37cb':'wx632d4c99bd681cf3'
    }
    static is_cosleep_ios(){
        let ios=false
        try {
            ios=typeof window.webkit.messageHandlers.XinchaoApp!='undefined'
        }catch (e) {
            //获取出错
        }
        return ios
    }
    static is_cosleep_android(){
        return typeof window.XinchaoApp!='undefined'
    }
    static getQueryVariable(variable) {
        var query = window.location.search.substring(1);
        var vars = query.split('&');
        for (var i = 0; i < vars.length; i++) {
            var pair = vars[i].split('=');
            if (decodeURIComponent(pair[0]) == variable) {
                return decodeURIComponent(pair[1]);
            }
        }
        console.log('Query variable %s not found', variable);
    }
    static callAppRouter(method, params={}, callback){
        let req = {
            'Method': method,
            'Data': params
        },cbName = `CB_${Date.now()}_${Math.ceil(Math.random() * 10)}`;
        req=JSON.stringify(req);
        Xinchao.Web[cbName] =  (err, result) => {
            if(callback){
                callback(err, result);
            }
            delete Xinchao.Web[cbName];
        };
        if (/(iPhone|iPad|iPod)/i.test(navigator.userAgent)) {
            window.webkit.messageHandlers.XinchaoApp.postMessage({req, cbName});
        } else {
            XinchaoApp.callRouter(req, cbName);
        }
    }
}


