﻿
<template>
    <div class="home">
        <div class="logo">
            <img src="./childrenday_logo.png" alt="">
        </div>
        <div class="title">
            <img src="./start_title@3x.png" alt="">
        </div>
        <div class="people">
            <img src="./start_img@3x.png" alt="">
        </div>
        <div class="test" @click="start">
            <div class="test-start">
                <img src="./button@3x.png" alt=""> <span class="test-text">开始测试</span>
            </div>
        </div>
        <div class="download" @click="download">
            <img src="./childrenday_start_tocosleep_btn.gif" alt="">
        </div>
        <div v-show="displayModal" class="modal">
            <div class="backdrop"></div>
            <div class="modal-input">
                <div class="input-container">
                    <input v-model="userName" v-on:keyup.enter="inputNameOk" type="text" placeholder="输入名字">
                </div>
                <div class="input-ok" @click="inputNameOk">确定</div>
            </div>
        </div>
    </div>
</template>
<script type="text/ecmascript-6">
    import axios from 'axios';
    import Tool from '@/assets/tool';
    import {Toast} from 'mint-ui'
    export default {
        name: 'Home',
        data () {
            return {
                userName:'',
                displayModal:false
            };
        },
        created () {
            // axios.get(`${Tool.get_api_root()}/web/v1/upload/token`).then((data) => {
            //     let respData = data['data'];
            //     localStorage.setItem('siguoya:6f87H4FAig', JSON.stringify(respData['data']));
            // });
        },
        mounted () {
            setTimeout(() => {
                // this.autoStart();
            }, 20);
        },
        methods: {
            inputNameOk(){
                if (this.userName){
                    localStorage.setItem('siguoya:G386FDNyK7',this.userName)
                    this.displayModal=false
                    this.start()
                }else{
                    Toast('输完名字之后才可以测试哦')
                }
            },
            autoStart () {
                if (Tool.is_wx() && Tool.getQueryVariable('code') && !localStorage.getItem('siguoya:Gy6jKG2W26')) {
                    this.start();
                }
            },
            getUserInfoByCode (code) {
                let url = `${Tool.get_api_root()}/web/v1/wechat/user?code=${code}`;
                axios.get(url).then((resp) => {
                    let data = resp['data'];
                    if(data.status==1){
                        localStorage.setItem('siguoya:Gy6jKG2W26', JSON.stringify(data));
                        this.start()
                    }else {
                        Toast('微信授权获取信息失败，请稍后重试')
                    }
                });
            },
            start () {
                if (Tool.is_wx()) {
                    let userInfo = localStorage.getItem('siguoya:Gy6jKG2W26');
                    if (!userInfo) {
                        let code = Tool.getQueryVariable('code');
                        if (code) {
                            this.getUserInfoByCode(code);
                            return false
                        } else {
                            let app_id = Tool.get_wechat_app_id(), redirect_url =﻿encodeURIComponent(window.location.href);
                            let redirect = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=' + app_id + '&redirect_uri=' + redirect_url + '&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect';
                            window.location.href = redirect;
                            return;
                        }
                    }
                } else if (Tool.is_cosleep()) {
                    let userInfo = localStorage.getItem('siguoya:79XYF7kQ4z');
                    if (!userInfo) {
                        Tool.callAppRouter('Login', {}, (error, result) => {
                            localStorage.setItem('siguoya:79XYF7kQ4z', JSON.stringify(result));
                        });
                        return;
                    }
                }else {
                    let userInfo=localStorage.getItem('siguoya:G386FDNyK7');
                    if (!userInfo) {
                        this.displayModal=true
                        return false
                    }
                }
                this.$router.push({
                    path: `/ques`
                });
            },
            download () {
                let _hmt = _hmt || [];
                if(Tool.is_wx()){
                    _hmt.push(['_trackEvent', '小睡眠儿童节活动', '点击下载', '首页点击-微信渠道']);
                }else if(Tool.is_cosleep_ios()){
                    _hmt.push(['_trackEvent', '小睡眠儿童节活动', '点击下载', '首页点击-小睡眠IOS版渠道']);
                }else if(Tool.is_cosleep_android()){
                    _hmt.push(['_trackEvent', '小睡眠儿童节活动', '点击下载', '首页点击-小睡眠Android版渠道']);
                }else {
                    _hmt.push(['_trackEvent', '小睡眠儿童节活动', '点击下载', '首页点击-其他渠道']);
                }
                window.location.href = 'https://www.heartide.com/download/smallsleep';
            }
        }
    };
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
    @import '~@/assets/mixin.styl'
    $fixed-size = 20px
    px2html(value)
      return unit(value / 37.5, 'rem')
    .home
        position fixed
        top $fixed-size
        bottom $fixed-size
        left $fixed-size
        right $fixed-size
        display flex
        flex-direction column
        .logo
            flex 1 0 auto
            img
                width 2.5rem
                height auto
                margin-top 5px
                margin-left 5px
        .title
            flex 1 0 auto
            text-align center
            img
                width 90%
                max-width 400px
                margin 0 auto
        .people
            flex 1 0 auto
            text-align center
            position relative
            top px2html(-40px)
            img
                width 6rem
                max-width 350px
        .test
            flex 1 0 auto
            margin 0 auto
            width 50%
            text-align center
            .test-start
                display inline-block
                position relative
                img
                    width 100%
                    max-width 320px
                    height auto
                .test-text
                    position absolute
                    top 50%
                    left 50%
                    width 100%
                    transform translate(-50%, -50%)
                    font-size px2html(15px)
                    color rgba(255, 255, 255, 1)
        .download
            flex 1 0 auto
            text-align center
            img
                margin-top .3rem
                width 3rem
        .backdrop
            position fixed
            top 0
            left 0
            width 100%
            height 100%
            background-color #000
            opacity .3
        .modal-input
            position fixed
            width 80%
            top 50%
            left 50%
            transform translate(-50%,-50%)
            text-align center
            background: radial-gradient(circle at top left, transparent .4rem, #FEFCEF 0) top left,
                radial-gradient(circle at top right, transparent .4rem, #FEFCEF 0) top right,
                radial-gradient(circle at bottom right, transparent .4rem, #FEFCEF 0) bottom right,
                radial-gradient(circle at bottom left, transparent .4rem, #FEFCEF 0) bottom left
            background-size: 51% 51%
            background-repeat: no-repeat
            .input-container
                margin 1rem 0 .6rem 0
                input
                    opacity .5
                    font-size 20px
                    color #754C24
                    height 40px
                    text-align center
                    border 1px solid #754C24
            .input-ok
                text-align center
                width 50%
                margin 0 0 .5rem 25%
                font-dpr(.4rem)
                padding .3rem 0
                background-color #E9B44B
                color #754C24
</style>



